<template>
    <v-toolbar class="navbar" flat dense>
    <v-toolbar-side-icon class="hidden-md-and-up"></v-toolbar-side-icon>
    <v-toolbar-title>
        <img class="logo" :class="{'animated pulse': isHover}" @mouseover="isHover=true" @mouseleave="isHover=false" src="../assets/logo.png">
    </v-toolbar-title>
    <span>BY Junlin Guo</span>
    <v-spacer></v-spacer>
    <v-toolbar-items class="hidden-sm-and-down">
      <template v-if="user.username">
        <v-btn color="primary" flat to='/chatroom'><v-avatar size="30px" class="grey lighten-4 mr-1"><img :src="user.avatar" alt="avatar"></v-avatar>{{user.username}}</v-btn>
        <v-btn flat @click="showAbout=true">About</v-btn>
        <v-btn flat @click="showContact=true">Contact</v-btn>
      </template>
      <template v-else>
        <v-btn color="primary" flat to='/' exact></v-btn>
        <v-btn color="primary" flat to='/login'></v-btn>
      </template>
    </v-toolbar-items>
    <vue-dialog v-if="showAbout" dialogName="about">
        <template slot="title"></template>
        <template slot="content">
            <br/><hr/>
            : <br/>1. <br/>2.<br/>
            <br/><hr/>~bug
        </template>
    </vue-dialog>
    <vue-dialog v-if="showContact" dialogName="contact">
        <template slot="title"></template>
        <template slot="content">
            <v-icon class="grey--text text--lighten-1 mr-2">mdi-email</v-icon><span>junlin.guo11@gmail.com</span><br>
            <v-text-field
            name="input"
            label="bug~~~"
            v-model="report"
            v-validate="'required'"
            textarea
            required
            ></v-text-field>
            <v-btn color="primary" @click="sendReport" :loading="loading" :disabled="loading"></v-btn>
        </template>
    </vue-dialog>
    </v-toolbar>
</template>

<script>
    import { sendReport } from '@/api/report';
    import EventBus from '../eventBus';
    import VueDialog from './VueDialog.vue';
    import { mapState } from 'vuex'
    export default {
        name: "navbar",
        components: { VueDialog },
        data() {
            return {
                title: "Chat App",
                showAbout: false,
                showContact: false,
                isHover: false,
                report: '',
                loading: false,
            }
        },
        computed: {
            ...mapState('user', ['user'])
        },
        methods: {
            sendReport: function() {
                this.$validator.validateAll().then(result => {
                    if(result) {
                        this.loading = true;

                        sendReport(this.report)
                            .then(() => {
                                this.loading = false;
                                this.report = "";
                            });
                    }
                })
            }
        },
        created() {
            EventBus.$on('closeDialog', (dialogName) => {
                if(dialogName === 'about') {
                    this.showAbout = false;
                } else {
                    this.showContact = false;
                }
            });          
        }
    }
</script>
<style scoped>
    .navbar {
        background-color: rgba(255, 255, 255, 0.9);
    }
    .logo {
        width: 3.5vmax;
        margin-left: 1vmax;
        margin-top: 10px;
        margin-right: 1vmin;
    }

    @supports (-ms-ime-align:auto) {
        .logo {
            width: calc(3.5 * (1vw + 1vh - 1vmin));;
            margin-left: calc(1 * (1vw + 1vh - 1vmin));
            margin-top: 10px;
        }
    }
</style>
